.wrapper {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  z-index: 999;
  background-color: rgb(0, 0, 0, 0.9);
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: column;
  width: 100%;
  text-align: center;
  align-items: center;
  padding: 15px;
}

.photo {
  max-height: 100%;
  max-width: 1000px;
}

.close {
  position: absolute;
  top: 20px;
  right: 100px;
}
